import {getFans, getFollow} from '../../api/api.js';
export default {
	data() {
		return {
			fnasList: '',
			butText:'',
			allFollow:[],
			list:[],
			textColor: ['#4CD964', '#4CD964', '#4CD964']
		}
	},
	methods: {
		getAllFans() {
			getFans().then(data => {
				this.fnasList = data;
				// console.log(data);
			});
			uni.hideLoading();
		},
		// 用户详情
		userInfo(id) {
			uni.navigateTo({
				url: '/pages/daily/userInfo?uid=' + id
			});
		},
		getAllFollow(){
			getFollow().then(data => {
				this.allFollow = data;
				for(let i = 0 ; i<this.allFollow.length;i++){	
						this.list.push(this.allFollow[i].id);
					};
				});
				this.follow()
		},
		follow(){
			for(let i = 0 ; i < this.fnasList.length; i++){
				console.log('1')
				if(this.list.indexOf(this.fnasList[i].id) != -1 ){
					console.log('2')
				}
			}
		}
		
		
	},
	onLoad() {
		uni.showLoading({
			title: '加载中'
		});
		this.getAllFans();
		this.getAllFollow();
	}
	
}
